
.signin-block{
  background: #DE6262;  /* fallback for old browsers */
background: -webkit-linear-gradient(to bottom, #FFB88C, #DE6262);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to bottom, #FFB88C, #DE6262); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
float:left;
width:100%;
height: 700%;
padding : 50px 0;
display: flex;
flex-direction: row;
flex-wrap: wrap;
justify-content: center;
align-items: baseline;
align-content: stretch;

}

.imagefav{
  display:block;
  margin:auto;
  max-width: 300px;
  max-height: 200px;
  border: 2px solid red;
  border-radius: 25px;
  width: 200px;
  height: 200px;

}

.userName{
  text-align: center;
  font-size: 18px;
  font-family: "Arial Black", Gadget, sans-serif;
 
  letter-spacing: 0px;
  word-spacing: 2px;
  color: #a2282a;
  font-weight: 700;
  text-decoration: underline solid rgb(68, 68, 68);
  font-style: normal;
  font-variant: normal;
  text-transform: uppercase;
}

.nameFav{
  text-align: center;
  font-family: Georgia, serif;
  font-size: 15px;
  letter-spacing: 2px;
  word-spacing: 2px;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  font-style: normal;
  font-variant: normal;
  text-transform: none;

}

.userEmail{
  text-align: center;
  font-family: Georgia, serif;
  font-size: 15px;
  letter-spacing: 2px;
  word-spacing: 2px;
  color: #000000;
  font-weight: 700;
  text-decoration: none;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
}
.userCity{
  text-align: center;
  font-family: Georgia, serif;
font-size: 25px;
letter-spacing: 2px;
word-spacing: 2px;
color: #000000;
font-weight: 700;
text-decoration: none;
font-style: normal;
font-variant: normal;
text-transform: none;
}
.userDescription{
  text-align: center;
  font-size: 20px;
}

.button.is-danger{
  display: block;
  margin-left: auto;
  margin-right: auto;


}

.gridfav{
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: stretch;
	align-content: stretch;
}

.marginfav{
  margin: 30px;
}

.details{
  color: white;
}